<template>
  <div ref="greenCkecker" :style="slidePosition" class="h-100 z-2 greenCkecker" style="width:100%;">
    <inline-svg src="images/Slide22/green-grid-boxes.svg" class="position-absolute" width="80%"
      aria-label="Table elements" style="bottom:0%;"></inline-svg>
  </div>
</template>

<script>
import anime from "animejs"
import { normalize } from "@/utils/ranges"
import { slidePositionPlain } from "@/utils/slidePositioning"
export default {
  props: ["globalUnits", "slideIndex"],
  computed: {
    slidePosition() {
      //console.log("SLIDES INDEX", this.slideIndex)
      return slidePositionPlain(this.globalUnits, this.slideIndex)
    },
    innerPosition() {
      return (
        (this.globalUnits - this.slideIndex.start) /
        (this.slideIndex.end - this.slideIndex.start)
      )
    }
  },
  mounted() { },
  methods: {
    greenCkecker(percentage) {
      let animation = anime.timeline({
        autoplay: false,
        loop: false,
        endDelay: 200
      })

      animation.add({
        targets: ".greenCkecker",
        opacity: [0, 1],
        duration: 600,
        delay: 0,
        easing: "easeInOutSine"
      })
      var progress =
        percentage <= 0.5 ? percentage * 2 : normalize(percentage, 0.5, 1, 1, 0)
      animation.seek(animation.duration * progress)
      //animation.seek(animation.duration * percentage)
    }
  },
  watch: {
    innerPosition() {
      this.greenCkecker(this.innerPosition)
    }
  }
}
</script>
<style lang="scss" scoped></style>
